import { Link } from "react-router-dom";
import { motion } from "framer-motion";
import { ArrowLeft } from "lucide-react";

export default function BlogCard({ blog }) {
  // Slugify the blog title
  const slug = encodeURIComponent(blog.title.trim().replace(/\s+/g, "-"));

  return (
    <motion.div
      whileHover={{ scale: 1.02, y: -5 }}
      transition={{ type: "spring", stiffness: 300 }}
    >
      <Link
        to={`/blogs/${slug}`}
        className="block bg-white shadow-sm rounded-2xl overflow-hidden border hover:shadow-xl transition-all duration-300"
        aria-label={`Read blog: ${blog.title}`}
        title={blog.title}
      >
        <img
          src={blog.featured_image}
          alt={`تصویر شاخص: ${blog.title}`}
          className="w-full h-52 object-cover"
          loading="lazy"
        />

        <div className="p-5 space-y-2 text-right">
          <h2 className="text-xl font-bold text-gray-800 leading-snug">
            {blog.title}
          </h2>
          <p className="text-sm text-gray-600 line-clamp-3">{blog.excerpt}</p>

          <div className="flex items-center gap-1 text-blue-600 mt-3 text-sm font-medium hover:underline transition-all">
            <ArrowLeft className="w-4 h-4" />
            <span>ادامه مطلب</span>
          </div>
        </div>
      </Link>
    </motion.div>
  );
}
