import BlogCard from "./BlogCard";

export default function BlogList({ blogs }) {
    return (
        <section className="py-10 px-6">
            <div className="max-w-6xl mx-auto grid xs:grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6">
                {blogs.map(blog => (
                    <BlogCard key={blog.id} blog={blog} />
                ))}
            </div>

            {blogs.length === 0 && (
                <p className="text-center text-gray-500 mt-10">مقاله‌ای یافت نشد.</p>
            )}
        </section>
    )
}