import BlogHero from "../ComponentChilds/Blog/BlogHero";
import BlogList from "../ComponentChilds/Blog/BlogList";
import { lazy, useEffect, useState } from "react";
import Pagination from "../ComponentChilds/Pagination";
// import BlogSidebar from "./BlogSidebar"; // optional
// import Footer from "../ComponentChilds/Footer";
// import Header from "../ComponentChilds/Header";
// import Loader from "../ComponentChilds/Loader";
import { Link, useSearchParams } from "react-router-dom";
import { Home, HomeIcon } from "lucide-react";
import { Helmet } from "react-helmet";
const Header = lazy(() => import("../ComponentChilds/Header"));
const Footer = lazy(() => import("../ComponentChilds/Footer"));
const Loader = lazy(() => import("../ComponentChilds/Loader"));

export default function BlogsPage({ basketItemCount }) {
  const [isLoading, setIsLoading] = useState(false);
  const [allBlogs, setAllBlogs] = useState([]);
  const [error, setError] = useState(null);
  const [isError, setIsError] = useState(false);
  const [searchParams, setSearchParams] = useSearchParams();
  const currentPage = parseInt(searchParams.get("page") || "1");

  const blogsPerPage = 6;

  useEffect(() => {
    const API_URL =
      window.location.hostname === "localhost"
        ? "http://127.0.0.1:8000/api/blog/"
        : "https://allmaxsport.com/api/blog/";

    setIsError(false);
    setIsLoading(true);
    async function fetchBlogs() {
      try {
        const response = await fetch(API_URL);
        const data = await response.json();
        setAllBlogs(data);
      } catch (err) {
        setIsError(true);
        setError(`متاسفانه خطایی رخ داد لحظاتی دیگر دوباره امتحان کنید.`);
      } finally {
        setIsLoading(false);
      }
    }
    fetchBlogs();
  }, []);

  const indexOfLast = currentPage * blogsPerPage;
  const indexOfFirst = indexOfLast - blogsPerPage;
  const currentBlogs = allBlogs.slice(indexOfFirst, indexOfLast);

  return (
    <div className="flex flex-col min-h-screen">
      <Header basketItemCount={basketItemCount} />

      <Helmet>
        <title>وبلاگ تخصصی بدنسازی | مقالات و آموزش‌ها | آلمکس اسپورت</title>
        <meta
          name="description"
          content="مقالات تخصصی بدنسازی، راهنمای خرید تجهیزات ورزشی، و آموزش‌های ورزشی در وبلاگ آلمکس اسپورت. با ما حرفه‌ای‌تر تمرین کنید."
        />
        <meta
          name="keywords"
          content="وبلاگ بدنسازی, مقالات بدنسازی, تجهیزات ورزشی, آلمکس اسپورت, آموزش بدنسازی, فیتنس, برنامه تمرینی"
        />
        <meta name="robots" content="index, follow" />
        <link rel="canonical" href="https://allmaxsport.com/blogs" />

        {/* Open Graph for social sharing */}
        <meta
          property="og:title"
          content="وبلاگ آلمکس اسپورت | مقالات بدنسازی"
        />
        <meta
          property="og:description"
          content="جدیدترین مقالات ورزشی و بدنسازی از آلمکس اسپورت، مرجع تخصصی تجهیزات باشگاهی و خانگی"
        />
        <meta property="og:type" content="website" />
        <meta property="og:url" content="https://allmaxsport.com/blogs" />
        <meta
          property="og:image"
          content="https://allmaxsport.com/images/blog-cover.jpg"
        />

        {/* Schema.org Structured Data */}
        <script type="application/ld+json">
          {JSON.stringify({
            "@context": "https://schema.org",
            "@type": "Blog",
            name: "وبلاگ آلمکس اسپورت",
            url: "https://allmaxsport.com/blogs",
            description:
              "مقالات و آموزش‌های تخصصی در زمینه تجهیزات ورزشی و بدنسازی",
            publisher: {
              "@type": "Organization",
              name: "آلمکس اسپورت",
              logo: {
                "@type": "ImageObject",
                url: "https://allmaxsport.com/logo.jpg",
              },
            },
          })}
        </script>
      </Helmet>

      {isLoading && <Loader color="black">لطفا صبر کنید...</Loader>}

      {isError && (
        <div className="min-h-[61vh] text-center">
          <div className="bg-red-500 px-6 py-4 rounded-lg w-1/4 text-white mx-auto my-12 text-center">
            <p className="font-bold">{error}</p>
            <Link
              className="bg-white px-4 py-2 rounded-lg mx-auto mt-1 text-red-500 font-bold flex flex-row gap-1 items-center justify-center w-1/2 hover:scale-105 duration-150"
              to={"/"}
            >
              <HomeIcon />
              <button>بازگشت به خانه</button>
            </Link>
          </div>
        </div>
      )}

      {!isLoading && !isError && currentBlogs.length > 0 && (
        <main className="font-vazir text-right bg-gray-50 flex-grow">
          <BlogHero />
          <BlogList blogs={currentBlogs} />
          <Pagination
            currentPage={currentPage}
            totalPages={Math.ceil(allBlogs.length / blogsPerPage)}
            onPageChange={(page) => setSearchParams({ page: page.toString() })}
          />
        </main>
      )}

      {!isLoading && !isError && currentBlogs.length === 0 && (
        <div className="flex-grow flex flex-col items-center px-8 py-6 rounded-lg bg-red-600 text-white w-full max-w-md my-8 mx-auto shadow-lg">
          <h4 className="text-2xl font-bold">متأسفیم!</h4>
          <p className="text-center mt-2">
            به نظر می‌رسد هیچ وبلاگی برای نمایش وجود ندارد.
          </p>
          <Link
            to="/"
            className="flex items-center justify-center mt-4 px-4 py-2 gap-1 bg-white text-red-600 rounded-lg hover:bg-red-200 transition-colors duration-200"
          >
            <Home className="w-5 h-5 mr-1" />
            <span>برو به صفحه اصلی</span>
          </Link>
        </div>
      )}

      <Footer />
    </div>
  );
}
