import { Pencil } from "lucide-react";
import { motion } from "framer-motion";

export default function BlogHero() {  
    return (  
      <motion.section  
        className="bg-gradient-to-r from-blue-500 to-blue-700 text-white py-14 md:py-20 px-6 text-center" // Fixed spelling  
        initial={{ opacity: 0, y: 30 }}  
        animate={{ opacity: 1, y: 0 }}  
        transition={{ duration: 0.6 }}  
      >  
        <div className="max-w-4xl mx-auto">  
          <h1 className="text-4xl font-bold mb-4 flex items-center justify-center gap-2">  
            <Pencil className="w-7 h-7" />  
            مقالات ورزشی و تجهیزات باشگاهی  
          </h1>  
          <p className="text-lg opacity-90">  
            جدیدترین نکات، آموزش‌ها و راهنمای خرید تجهیزات ورزشی برای باشگاه‌داران  
            حرفه‌ای  
          </p>  
        </div>  
      </motion.section>  
    );  
  }  